.page-footer {
  width: 100%;
  height: 112px;
  background-color: black;
}

.footer-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.footer-copyright {
  color: var(--basic-white);
  font-size: 1rem;
}

@media (max-width: 1179px) {
  .footer-container {
    padding: 0 1rem;
  }
}
