.main-nav {
  height: 100%;
}

.nav__toggle {
  margin-top: 20px;
  position: relative;
  right: 0;
  top: 0;
  bottom: 0;
  width: 40px;
  height: 40px;
  cursor: pointer;
  border: 0;
  background: 0 0;
}

.nav__toggle::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 8px;
  width: 24px;
  height: 3px;
  background-color: var(--bacis-orange);
  box-shadow: 0 9px 0 0 var(--bacis-orange), 0 18px 0 0 var(--bacis-orange);
}

.nav__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}


.nav__item {
  overflow: hidden;
  white-space: nowrap;
  margin-right: 26px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item a {
  color: var(--basic-white);
  text-decoration: none;
}

.nav__item a:hover,
.active__link {
  border-bottom: 1px solid var(--bacis-orange);
}